body {
    min-width: 1170px;
}
em.titles i{
    color: #878787;
}
em.titles.title2{
    color: #fff;
    i{
        color: #a9a9a9;
    }
}
.full {
    width: 100%;
    height: 100%;
}

.page-1 {
    min-width: 1170px;
    margin: 0 auto;
    div.swiper1 {
        width: 100%;
        height: 800px;

        div.swiper-slide-active {
            a.lazy {
                img {
                    display: block;
                }
            }
        }

        a {
            display: block;
            width: 100%;
            height: 800px;
            position: relative;

            img {
                display: block;
                width: 1600px;
                height: 800px;
                position: absolute;
                left: 50%;
                top: 0;
                margin-left: -800px;
                display: none;
            }
        }
    }

    div.content-page {
        .page1 {
            background-color: #f7f7f7;
            overflow: hidden;

            img {
                background: none;
                display: block;
                width: 1078px;
                height: 463px;
                margin: 50px auto;
            }
        }

        .page2 {
            background-color: #252525;
            overflow: hidden;

            em.titles {
                color: #fff;

                i {
                    color: #8f8f8f;
                }
            }

            div.hovers {
                margin-top: 80px;
                width: 1235px;

                div.hovers-list {
                    overflow: hidden;
                    float: left;
                    width: 270px;
                    height: 300px;
                    overflow: hidden;
                    position: relative;
                    text-align: center;
                    margin-right: 30px;

                    &:hover {
                        span {
                            bottom: -50px;
                        }

                        div.tops {
                            top: 0;
                        }
                    }

                    span {
                        display: block;
                        background-color: rgba(0,0,0,0.4);
                        width: 100%;
                        height: 50px;
                        line-height: 50px;
                        color: #fff;
                        font-size: 18px;
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        @include tran;
                    }

                    div.tops {
                        @include tran;
                        position: absolute;
                        left: 0;
                        top: -100%;
                        width: 100%;
                        height: 100%;
                        background-color: rgba(76,193,170,0.9);
                        font-size: 16px;
                        line-height: 2em;
                        color: #fff;

                        em {
                            margin-top: 45px;
                            margin-bottom: 10px;
                            display: inline-block;
                            border-bottom: 1px solid #fff;
                            padding-bottom: 12px;
                            font-size: 22px;
                        }
                    }
                }
            }

            div.peoples {
                width: 1250px;
                padding-bottom: 100px;
                margin-top: 70px;

                div.lists {
                    float: left;
                    margin-left: 5px;
                    margin-right: 56px;

                    .lazy-body {
                        border: 2px solid #474747;
                        width: 214px;
                        height: 214px;
                        padding: 13px;

                        .lazy {
                            display: block;
                            width: 214px;
                            height: 214px;
                        }
                    }

                    em {
                        text-align: center;

                        span {
                            color: #c59a6d;
                            margin: 15px 0;
                            display: block;
                        }

                        i {
                            display: block;
                            color: #777777;
                        }
                    }
                }
            }
        }

        .page3 {
            background-color: #f8f8f8;

            i.xin {
                &:before {
                    background-color: #f8f8f8;
                }
            }

            img {
                background: none;
                display: block;
                margin: 0 auto;
                width: 1098px;
                margin-top: 20px;
                height: 754px;
            }
        }

        .page4 {
            background-color: #fff;
            overflow: hidden;

            i.xin {
                &:before {
                    background-color: #fff;
                }
            }

            img.huo {
                width: 769px;
                height: 465px;
                margin: 60px auto;
                background: none;
            }

            div.swiper2 {
                margin-bottom: 30px;
                height: 260px;
                width: 100%;

                .pagination1 {
                    bottom: 0;
                }

                .swiper-pagination-switch {
                    background-color: transparent;
                    border: 1px solid #d1d1d1;
                }

                .swiper-active-switch {
                    background-color: #35bda3;
                    border-color: #35bda3;
                }
                width: 100%;

                div.swiper-slide {
                    height: 230px;
                }

                div.a-body {
                    width: 1200px;
                    overflow: hidden;
                }

                a {
                    float: left;
                    margin-right: 15px;
                    border: 1px solid #c7c7c7;
                    width: 180px;
                    height: 220px;
                    overflow: hidden;
                    text-align: center;

                    div.lazy {
                        width: 180px;
                        height: 112px;
                    }

                    span {
                        background-color: #35bda3;
                        color: #fff;
                        text-align: center;
                        font-size: 16px;
                        width: 120px;
                        display: block;
                        height: 30px;
                        line-height: 30px;
                        border-radius: 5px;
                        margin: 15px auto;
                    }

                    p {
                        width: 160px;
                        margin: 0 auto;
                        text-align: left;
                        @include line_over(2);
                        height: 42px;
                        color: #666;
                        font-size: 12px;
                    }
                }
            }
        }

        .page5 {
            background-color: #040404;
            overflow: hidden;

            em.titles {
                color: #fff;

                i {
                    color: #a9a9a9;
                }
            }

            div.qin {
                margin: 60px 0;
                overflow: hidden;
                height: 410px;
                position: relative;

                div.qin-list {
                    position: relative;
                    width: 70px;
                    @include tran($b:linear);
                    height: 100%;
                    background-color: #f46f46;
                    background-color: rgba(244,111,70,0.8);
                    float: left;
                    overflow: hidden;

                    &.even {
                        background-color: #4ab49f;
                        background-color: rgba(74,180,159,0.8);
                    }

                    &.on {
                        width: 820px;

                        div.qin-title {
                            opacity: 0;
                            top: 100%;
                        }

                        div.qin-left {
                            opacity: 1;
                        }
                    }
                }

                div.qin-left {
                    overflow: hidden;
                    text-align: center;
                    position: relative;
                    width: 760px;
                    height: 370px;
                    padding: 40px;
                    color: #fff;
                    opacity: 0;
                    overflow-y: auto;
                    @include tran($d:0.3s);

                    em {
                        display: block;
                        font-size: 30px;
                        margin-bottom: 25px;
                    }

                    span {
                        display: block;
                        font-size: 14px;
                    }

                    div {
                        margin-top: 30px;

                        i {
                            float: left;
                            text-align: left;
                            width: 150px;
                            height: 30px;
                            line-height: 30px;
                            font-size: 12px;
                            padding: 0 10px;
                            @extend .ellipsis;
                            background-color: rgba(0,0,0,0.08);
                            margin-right: 16px;
                            margin-bottom: 5px;
                        }
                    }
                }

                div.qin-title {
                    position: absolute;
                    width: 70px;
                    height: 100%;
                    color: #f8f8f8;
                    font-size: 20px;
                    overflow: hidden;
                    writing-mode: tb-rl;
                    text-align: center;
                    cursor: pointer;
                    right: 17px;
                    top: 0;
                    @include tran($d:0.3s);

                    i {
                        display: inline-block;
                        font-size: 24px;
                        writing-mode: lr-tb;
                    }
                }
            }
        }

        .page6 {
            padding-bottom: 100px;

            i.xin:before {
                background-color: #fff;
            }

            img {
                margin: 0 auto;
            }
        }


    }
}
body.page-1-2{
    .page7 {
        background-color: #2a2a2a;
        position: relative;
        height: 514px;
        overflow: hidden;
        img.bg{
            position: absolute;
            left:50%;
            top:0;
            margin-left: -800px;
            height: 514px;
        }
        div.w1170{
            position: relative;
            z-index: 3;
        }
        em.titles {
            span {
                color: #fff;
            }

            i {
                color: #aaaaaa;
            }
        }

        form {
            display: block;
            text-align: center;
            overflow: hidden;
            padding-bottom: 70px;

            div.input {
                display: inline-block;
                width: 370px;
                height: 50px;
                font-size: 14px;
                margin: 65px 15px 0;
                position: relative;

                div.error {
                    position: absolute;
                    top: 100%;
                    left: 0;
                    line-height: 30px;
                    display: block;
                    margin-top: 15px;
                    white-space: nowrap;
                }

                input,select {
                    box-sizing: border-box;
                    display: block;
                    width: 100%;
                    height: 100%;
                    text-indent: 1em;
                    border-radius: 0;
                }

            }

            button {
                margin-top: 65px;
                width: 370px;
                font-size: 14px;
                text-align: center;
                line-height: 50px;
                border: 1px solid #3cd8b9;
                color: #3cd8b9;
                background-color: transparent;
                cursor: pointer;
            }
        }
    }
}
body.page-2 {
    div.page-header{
        margin-top:70px;
        position: relative;
        height: 40px;
        overflow: hidden;
        position: absolute;
        z-index: 10;
        left:0;
        top:0;
        width: 100%;
        i.bg{
            position: absolute;
            left:0;
            top:0;
            background-color: #fff;
            @include opacity(0.2);
        }
        div.w1170{
            position: relative;
            z-index: 1;
            div.fl{
                line-height: 40px;
                a{
                    color: #fff;
                    @include tran;
                    margin-right:30px;
                    &:hover{
                        @extend .on;
                    }
                    &.on{
                        color: $color_logo;
                    }
                }
            }
            div.fr{
                a{
                    display: block;
                    background-color: $color_logo;
                    border-radius: 3px;
                    color: #fff;
                    font-size: 12px;
                    padding:6px 15px;
                    line-height: 1em;
                    margin-top:7px;
                    @include tran;
                    &:hover{
                        @include opacity(0.8);

                    }
                }
            }
        }
    }
    div.page2{
        div.fonts{
            position: absolute;
            z-index: 10;
            width: 100%;
            top:400px;
            left:95px;
            width: 1000px;
            p{
                float: left;
                width: 33%;
                text-align: center;
                color: #fff;


            }
        }
    }
    .go-form{
        cursor: pointer;
    }
    div.banner {
        background-color: #000;
        height: 800px;
        position: relative;

        img {
            position: absolute;
            left: 50%;
            margin-left: -800px;
            top: 0;
            display: block;
            height: 800px;
            background: none;
            &.banner-top{
                height: 319px;
                margin-left:-239px;
                top:287px;
            }
        }

        a {
            display: block;
            position: absolute;
            width: 110px;
            height: 30px;
            color: #fff;
            background-color: #1ea78d;
            text-align: center;
            line-height: 30px;
            font-size: 14px;
            left: 50%;
            margin-left: -55px;
            bottom: 180px;
            @include tran;

            &:hover {
                background-color: #fff;
                color: #1ea78d;
            }
        }
    }

    div.page1 {
        background-color: #f7f7f7;
        overflow: hidden;
        padding-bottom: 70px;
        ul.swiper-btns {
            position: relative;
            height: 50px;
            border-bottom: 1px solid #f0f0f0;

            li {
                float: left;
                width: 20%;
                position: relative;
                height: 50px;
                line-height: 50px;
                text-align: center;
                color: #666;
                font-size: 14px;
                @include tran;
                @extend .no_select;
                cursor: pointer;

                &:hover {
                    @extend .on;
                }

                &.on {
                    color: #1ea78d;
                }

                &:after {
                    content: '';
                    position: absolute;
                    width: 1px;
                    display: block;
                    height: 14px;
                    background-color: #b7ddd6;
                    right: 0;
                    top: 50%;
                    margin-top: -7px;
                }
            }

            i {
                position: absolute;
                width: 20%;
                height: 2px;
                background-color: #1ea78d;
                bottom: -1px;
                left: 0;
                display: block;
                @include  tran;
            }

            &:after {
                content: '';
                position: absolute;
                width: 1px;
                display: block;
                height: 20px;
                background-color: #f7f7f7;
                right: 0;
                top: 50%;
                margin-top: -7px;
            }
        }

        div.swiper1 {
            width: 100%;
            height: 450px;
            overflow: hidden;

            a.lazy {
                display: block;
                width: 1170px;
                height: 450px;
                img{
                    display: none;
                }
            }
            .swiper-slide-active{
                a.lazy{
                    img{
                        display: block;
                    }
                }
            }
        }
    }
    div.page2{
        background-color: #35b099;
        div.w1170{
            height: 527px;
            position: relative;
            img{
                position: absolute;
                height: 100%;
                background-color: transparent;
                top:0;
                left:50%;
                margin-left:-800px;
            }
        }
    }
    div.page3{
        background-color: #fff;
        overflow: hidden;
    }
    div.page4{
        background-color: #0c0c0c;
        position: relative;
        height:705px;
        overflow: hidden;
        img.bg{
            position: absolute;
            height: 705px;
            left:50%;
            top:0;
            margin-left:-800px;
            background-color: transparent;
        }
        div.w1170{
            position: relative;
            z-index: 3;
            div.swiper-body{
                position: relative;
                width: 1030px;
                margin:0 auto;
                i.icons{
                    position: absolute;
                    display: block;
                    top:50%;
                    margin-top:-36px;
                    border:1px solid #fff;
                    opacity: 0.6;
                    width: 70px;
                    height: 70px;
                    border-radius: 50%;
                    text-align: center;
                    line-height: 70px;
                    font-size: 50px;
                    right:100%;
                    color: #fff;
                    opacity: 0.6;
                    cursor: pointer;
                    &.icon-right{
                        left:100%;
                    }
                }
            }
            div.swiper2{
                width: 970px;
                height: 420px;
                margin:50px auto;
                position: relative;

                div.swiper-slide{
                    position: relative;
                    background-color: #1f1f1f;
                    overflow: hidden;
                    img{
                        display: none;
                    }
                    &.swiper-slide-active{
                        img{
                            display: block;
                        }
                        div.top{
                            div.left{
                                left:0;
                            }
                            div.right{
                                div.lazy{
                                    @include transform(translate(0,0));
                                }
                            }
                        }
                    }
                    div.top{
                        position: absolute;
                        left:0;
                        top:0;
                        z-index: 10;
                        div.left{
                            width: 270px;
                            position:absolute;
                            height: 100%;
                            overflow: hidden;
                            top:0;
                            left:-300px;
                            @include tran($c:0.5s,$d:0.4s);
                            div.lazy{
                                width: 200px;
                                height: 140px;
                                background-color: #fff;
                                border:1px solid #e9e9e9;
                                margin:60px auto;
                                margin-bottom:20px;
                            }
                            div.fonts{
                                width: 200px;
                                overflow-y: auto;
                                color: #666;
                                height: 180px;
                                margin:0 auto;
                                font-size: 12px;
                            }
                            &:after{
                                width: 100%;
                                height: 100%;
                                background-color: #fff;
                                opacity: 0.8;
                                content: '';
                                left:0;
                                top:0;
                                position: absolute;
                                z-index: -1;
                            }
                        }
                        div.right{
                            position: absolute;
                            bottom:0;
                            left:270px;
                            right:0;
                            height: 150px;
                            text-align: center;
                            font-size: 0;
                            overflow: hidden;
                            div.lazy{
                                display: inline-block;
                                width: 130px;
                                height: 110px;
                                margin:0 20px;
                                border:6px solid #fff;
                                @include transform(translate(0,150%));
                                @include tran($c:0.5s,$d:0.5s);
                                &:nth-of-type(2){
                                    @include tran($c:0.5s,$d:0.6s);
                                }
                                &:nth-of-type(3){
                                    @include tran($c:0.5s,$d:0.7s);
                                }
                            }
                        }
                    }
                }

            }
        }
    }
    div.page7{
        overflow: hidden;
        span.xin{
            opacity: 0.3;
        }
        button{
            margin-top:0px;
        }
        div.input{

            background-color: #fff;
            display: block;
            margin:16px auto;
            input,select{
                font-size: 14px;


            }

            div.error{
                left:390px;
                top:0;
            }
        }
    }
}
div.input input{
    @include tran;
    &:focus{
        border-color:$color_logo;
        box-shadow: 2px 1px 6px $color_logo;
    }
}
select{
    border:none;
}
.pagination,
.pagination1 {
    position: absolute;
    z-index: 20;
    bottom: 10px;
    width: 100%;
    text-align: center;
}

.swiper-pagination-switch {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 8px;
    background: #555;
    margin: 0 5px;
    opacity: 0.8;
    border: 1px solid #fff;
    cursor: pointer;
}

.swiper-active-switch {
    background: #fff;
}
